<template>
  <div class="rec">
    <van-pull-refresh v-model="refreshing"
                      :style="`min-height: calc(100vh - ${headHeight}px)`"
                      @refresh="onRefresh">
      <van-list v-model="loading"
                :finished="finished"
                :finished-text="$t('noMore')"
                @load="onLoad">
        <template v-if="type == 2">
          <DestroyReceivingRecord v-for="(el, index) in list2"
                                  :item="el"
                                  :key="el.id" />
        </template>
        <template v-if="type == 1">
          <DestroyRecord v-for="(el, index) in list1"
                         :item="el"
                         :key="el.id" />
        </template>
      </van-list>
    </van-pull-refresh>
  </div>
</template>
<script>
import DestroyReceivingRecord from './components/DestroyReceivingRecord'
import DestroyRecord from './components/DestroyRecord'
import {
  record,
  burnRecord
} from '@/api/request.js'
export default {
  components: {
    DestroyReceivingRecord,
    DestroyRecord
  },
  data () {
    return {
      type: 0, // 1销毁记录2领取记录
      list1: [], // 销毁记录
      list2: [], // 领取记录
      loading: false, // 加载中
      finished: false, // 加载完成
      refreshing: false, // 下拉刷新
      page: 1,
      headHeight: 162,
      heightPlay: 0, //元素高
      observer: null
    }
  },
  beforeRouteEnter (to, from, next) {
    if (to.query.type == 1) {
      to.meta.title = 'meta.destroyRecords'
    }
    if (to.query.type == 2) {
      to.meta.title = 'meta.NFTrecords'
    }
    next()
  },
  methods: {
    async onLoad () {
      if (this.refreshing) {
        this.list1 = [];
        this.list2 = [];
        this.page = 1
        this.refreshing = false;
      }
      let data = {
        page: this.page,
        size: 10
      }
      if (data.page == 1) this.list2 = [], this.list1 = [];
      let res = ''
      if (this.type == 1) {
        res = await burnRecord(data)
      } else {
        res = await record(data)
      }
      this.list2 = this.list2.concat(res.records)
      this.list1 = this.list1.concat(res.records)
      // this.list.ass(res.records)
      this.loading = false;
      if ((this.list2.length || this.list1.length) == res.total) {
        this.finished = true;
      } else {
        this.page = this.page + 1
      }
    },
    onRefresh () {
      this.page = 1
      this.finished = false;
      this.loading = true;
      this.onLoad();
    },
  },
  created () {
    this.type = this.$route.query.type
  },
  mounted () {
    let ResizeObserver = window.ResizeObserver
    this.observer = new ResizeObserver(() => {
      let heightPlay = document.getElementsByClassName('heads')[0].offsetHeight
      if (this.heightPlay != heightPlay) {
        this.headHeight = heightPlay + 10
      }
    });
    this.observer.observe(document.querySelector('.heads'), {
      attributes: true,
      attributeFilter: ['style'],
      attributeOldValue: true
    })
  },
  beforeDestroy () {
    if (this.observer) {
      this.observer.disconnect()
      this.observer = null
    }
  }
}
</script>
<style lang="less" scoped>
.rec {
  padding: 5px 10px;
}
</style>