<template>
  <div class="item">
    <div class="left">
      <div class="num">
        <div class="val">+{{item.amount}}</div>
        <div class="unit">SRS</div>
      </div>
      <div class="time">{{formData(item.createdTime)}}</div>
    </div>
    <van-button type="primary"
                class="rig"
                color="#1F75D9"
                v-if="item.isAvailable"
                @click="again(item)"
                block>{{$t('again')}}</van-button>
  </div>
</template>
<script>
import {
  NumFormat,
  getUnixToDate
} from '@/util/filter.js'
import BigToSmall from '@/util/BNUtils.js'
export default {
  props: ['item'],
  methods: {
    async again (res) {
      let mount = BigToSmall.pow(res.amount, 18)
      await this.$store.state.destroyContractInstance.methods
        .receiveAward(res.address, mount, res.sign)
        .send({
          from: res.address,
        })
        .then((res) => {
          // this.btnLoading = false
          // this.btnText = 'stake'
          console.log('res', res)
          // 查询授权数量
          // this.getAllowance()
        })
        .catch((err) => {
          Toast(this.$t('cancel'))
          // this.btnLoading = false
        })
    },
    formData (val) {
      return getUnixToDate('yyyy-mm-dd hh:mm', new Date(val) / 1000)
    }
  }
}
</script>
<style scoped lang="less">
.item {
  padding: 20px 10px;
  background: #ffffff;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
  .left {
    display: flex;
    flex-direction: column;
    .num {
      display: flex;
      align-items: flex-end;
      .val {
        font-size: 15px;
        font-weight: bold;
        color: #1577ff;
        line-height: 1;
      }
      .unit {
        font-size: 12px;
        transform: scale(0.75);
        transform-origin: left bottom;
        font-weight: bold;
        color: #909cba;
        margin-left: 7px;
      }
    }
    .time {
      font-size: 14px;
      font-weight: 500;
      color: #b1b5c5;
      margin-top: 14px;
    }
  }
  .rig {
    width: 70px;
    height: 27px;
    background: #1f75d9;
    border-radius: 13px;
    font-size: 12px;
    font-weight: 500;
    color: #ffffff;
    text-align: center;
    line-height: 27px;
    padding: 0;
    /deep/.van-button__content {
      line-height: 1;
    }
  }
}
</style>